import React, { Component, Fragment } from 'react';

import Grid from '@material-ui/core/Grid';
import {withStyles} from '@material-ui/core/styles'
import Divider from '@material-ui/core/Divider';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell';
import Typography from '@material-ui/core/Typography';

const styles = theme => ({
    divider: {
        marginTop: theme.spacing.unit * 2,
        marginBottom: theme.spacing.unit * 2
    },
    card: { 
        minHeight: '350px'
    },
    addieTitle: {
        paddingBottom: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        borderBottom: '3px solid #4e89eb'
    },
    addieA: {
        borderColor: '#4dabf5'
    },
    addieD1: {
        borderColor: '#5393ff'
    },
    addieD2: {
        borderColor: '#2196f3'
    },
    addieI: {
        borderColor: '#2979ff'
    },
    addieE: {
        borderColor: '#1769aa'
    },
    addieT: {
        borderColor: '#ff5722'
    },
    total: {
        fontWeight: 'bold'
    },
    big: {
        fontSize: '1rem'
    },
    numberCell: {
        textAlign: 'right'
    }
})

class CMI extends Component {
    constructor(props) {
        super(props);
        this.data = {
            ...this.props.data.shared,
            ...this.props.data.cmi,
            costPerIMIHour: 25000,
            TADLPContractCost: 0,
            devCostPerLearner: 0,
            GARLaborRate: 18.03,
            GARTime: 0,
            GARCost: 0,
            GARCostPerLearner: 0,
            FY14ALMSCost: 1.86,
            FTFieldingCost: 0
        }
    }

    calcAndSetData() {
        let workingData = { ...this.data, ...this.props.data.shared, ...this.props.data.cmi };

        let calculations = {
            TADLPContractCost: workingData.finishedIMIHours * workingData.costPerIMIHour,
            devCostPerLearner: (workingData.finishedIMIHours * workingData.costPerIMIHour) / workingData.learnerTargetAudience,
            GARTime: workingData.contractIMIHours * 1.6,
            GARCost: (workingData.contractIMIHours * 1.6) * workingData.GARLaborRate,
            GARCostPerLearner: ((workingData.contractIMIHours * 1.6) * workingData.GARLaborRate) / workingData.learnerTargetAudience,
            FTFieldingCost: workingData.FY14ALMSCost * workingData.contentPackages,
            ImplementationCostPerLearner: (workingData.FY14ALMSCost * workingData.contentPackages) / workingData.learnerTargetAudience,
            athdCost: Number(workingData.athdCost),
            athdLicenseCost: Number(workingData.athdLicenseCost),
            tktHours: workingData.tktHours,
            datrCost: Number(workingData.datrCost),
            EvaluationCostPerLearner: (Number(workingData.athdCost) + Number(workingData.athdLicenseCost) + Number(workingData.datrCost)) / workingData.learnerTargetAudience,
            TotalCost: 
                (workingData.finishedIMIHours * workingData.costPerIMIHour) 
                + (workingData.contractIMIHours * 1.6) * workingData.GARLaborRate,
            TotalCostPerLearner: 
                ((workingData.finishedIMIHours * workingData.costPerIMIHour) / workingData.learnerTargetAudience) 
                + (((workingData.contractIMIHours * 1.6) * workingData.GARLaborRate) / workingData.learnerTargetAudience) 
                + ((workingData.FY14ALMSCost * workingData.contentPackages) / workingData.learnerTargetAudience)
                + ((Number(workingData.athdCost) + Number(workingData.athdLicenseCost) + Number(workingData.datrCost)) / workingData.learnerTargetAudience)
        }

        this.data = { ...workingData, ...calculations }        
    }

    render () {
        const { classes } = this.props;
        
        this.calcAndSetData();
        return (
            <Fragment>
                <Typography variant="h4" gutterBottom>Computer-Managed Instruction (CMI)</Typography>
                <Typography variant="body1"gutterBottom>Distance learning. Content is auto-scoring, and the learner interacts with content with no instructor or classroom.</Typography>

                <Divider className={classes.divider} />

                <Typography variant="h5" gutterBottom>Costs by ADDIE Stage</Typography>

                <Grid container spacing={8}>
                    <Grid item xs={4}>
                        <Card className={classes.card}>
                            <CardContent>
                                <Typography variant="h6" className={[classes.addieTitle, classes.addieA].join(" ")}>
                                    Analysis
                                </Typography>
                                <Typography varient="body1">
                                    Costs are fixed. Activities include job analysis and other tasks performed by an ISD without the need for automated tools.
                                </Typography>
                            </CardContent>
                        </Card>
                    </Grid>
                    <Grid item xs={4}>
                        <Card className={classes.card}>
                            <CardContent>
                                <Typography variant="h6" className={[classes.addieTitle, classes.addieD1].join(" ")}>
                                    Design
                                </Typography>
                                <Typography variant="body1">
                                    Costs are fixed. Activies include design of TLOs, ELOs, sequencing, etc., done during TDC.
                                </Typography>
                            </CardContent>
                        </Card>
                    </Grid>
                    <Grid item xs={4}>
                        <Card className={classes.card}>
                            <CardContent>
                                <Typography variant="h6" className={[classes.addieTitle, classes.addieD2].join(" ")}>
                                    Development
                                </Typography>
                                <Table>
                                    <TableBody>
                                        <TableRow>
                                            <TableCell>Finished IMI Hours</TableCell>
                                            <TableCell className={classes.numberCell}>{this.data.finishedIMIHours}</TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Cost per IMI Hour</TableCell>
                                            <TableCell className={classes.numberCell}>${this.data.costPerIMIHour.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Learner Target Audience Throughput</TableCell>
                                            <TableCell className={classes.numberCell}>{this.data.learnerTargetAudience.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>TADLP Contract Cost</TableCell>
                                            <TableCell className={classes.numberCell}>${this.data.TADLPContractCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Development Cost Per Learner</TableCell>
                                            <TableCell className={classes.numberCell}>${this.data.devCostPerLearner.toFixed(2)}</TableCell>
                                        </TableRow>
                                    </TableBody>
                                </Table>
                            </CardContent>
                        </Card>
                    </Grid>
                    <Grid item xs={4}>
                        <Card className={classes.card}>
                            <CardContent>
                                <Typography variant="h6" className={[classes.addieTitle, classes.addieI].join(" ")}>
                                    Implementation
                                </Typography>
                                <Table>
                                    <TableBody>
                                        <TableRow>
                                            <TableCell>Impl. Br. GAR Labor Rate</TableCell>
                                            <TableCell className={classes.numberCell}>${this.data.GARLaborRate}</TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>IMI Hours</TableCell>
                                            <TableCell className={classes.numberCell}>{this.data.contractIMIHours}</TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Hours to Complete GAR</TableCell>
                                            <TableCell className={classes.numberCell}>{this.data.GARTime.toFixed(0)}</TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>GAR Cost</TableCell>
                                            <TableCell className={classes.numberCell}>${this.data.GARCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>GAR Cost per Learner</TableCell>
                                            <TableCell className={classes.numberCell}>${this.data.GARCostPerLearner.toFixed(2)}</TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>ALMS Cost per Training Unit</TableCell>
                                            <TableCell className={classes.numberCell}>${this.data.FY14ALMSCost}</TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Content Packages</TableCell>
                                            <TableCell className={classes.numberCell}>{this.data.contentPackages}</TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>FT and Fielding Cost per Learner</TableCell>
                                            <TableCell className={classes.numberCell}>${this.data.FTFieldingCost.toFixed(2)}</TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Implementation Cost per Learner</TableCell>
                                            <TableCell className={classes.numberCell}>${this.data.ImplementationCostPerLearner.toFixed(4)}</TableCell>
                                        </TableRow>
                                    </TableBody>
                                </Table>
                            </CardContent>
                        </Card>
                        
                    </Grid>
                    <Grid item xs={4}>
                        <Card className={classes.card}>
                            <CardContent>
                                <Typography variant="h6" className={[classes.addieTitle, classes.addieE].join(" ")}>
                                    Evaluation
                                </Typography>
                                <Table>
                                    <TableBody>
                                        <TableRow>
                                            <TableCell>Army Training Help Desk (ATHD)</TableCell>
                                            <TableCell className={classes.numberCell}>${this.data.athdCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>ATHD RightNow License Cost (for all Agents)</TableCell>
                                            <TableCell className={classes.numberCell}>${this.data.athdLicenseCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>TKT Hours</TableCell>
                                            <TableCell className={classes.numberCell}>{this.data.tktHours.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>DL DATR Cost</TableCell>
                                            <TableCell className={classes.numberCell}>${this.data.datrCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell>Evaluation Cost per Learner</TableCell>
                                            <TableCell className={classes.numberCell}>${this.data.EvaluationCostPerLearner.toFixed(4)}</TableCell>
                                        </TableRow>
                                    </TableBody>
                                </Table>
                            </CardContent>
                        </Card>

                    </Grid>
                    <Grid item xs={4}>
                        <Card className={classes.card}>
                            <CardContent> 
                                <Typography variant="h6" className={[classes.addieTitle, classes.addieT].join(" ")}>
                                    Totals
                                </Typography>
                                <Table>
                                    <TableBody>
                                        <TableRow>
                                            <TableCell className={[classes.total, classes.big].join(" ")}>
                                                Total Cost
                                            </TableCell>
                                            <TableCell numeric className={[classes.numberCell, classes.total, classes.big].join(" ")}>
                                                ${this.data.TotalCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell className={classes.total}>
                                                Total Cost per Learner
                                            </TableCell>
                                            <TableCell numeric className={[classes.total, classes.numberCell].join(" ")}>
                                                ${this.data.TotalCostPerLearner.toFixed(2)}
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell className={classes.total}>
                                                Total Cost per Learner if Rebuilt after 3 Years
                                            </TableCell>
                                            <TableCell numeric className={[classes.total, classes.numberCell].join(" ")}>
                                                ${(this.data.TotalCostPerLearner * 2).toFixed(2)}
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                </Table>
                            </CardContent>
                        </Card>
                    </Grid>
                </Grid>
            </Fragment>
        );
    }
}

export default withStyles(styles)(CMI);
