import React, { Component } from 'react';
import CMI from '../../components/TrainingType/CMI';
import CAI from '../../components/TrainingType/CAI';
import Live from '../../components/TrainingType/Live';
import Fields from '../../components/TrainingType/Fields';

import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Divider from '@material-ui/core/Divider'
import TextField from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment'
import RadioGroup from '@material-ui/core/RadioGroup';
import Radio from '@material-ui/core/Radio';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Typography from '@material-ui/core/Typography';


const styles = theme => ({
    root: {
        padding: theme.spacing.unit * 2,
        width: '100%'
    },
    paper: {
        padding: theme.spacing.unit * 2
    },
    textField: {
        marginBottom: theme.spacing.unit
    },
    divider: {
        marginTop: theme.spacing.unit * 2,
        marginBottom: theme.spacing.unit * 2
    }
})

class CostCalculator extends Component {
    constructor() {
        super();
        this.state = {
            selected: null,
            shared : {
                learnerCostPerDay: 1.61,
                loadedAnnualInstructorCost: 100000,
                TDYCostPerDay: 100,
                learnerTargetAudience: 600000,
                athdCost: 0,
                athdLicenseCost: 0,
                tktHours: 0,
                datrCost: 0,
                evaluationCostPerLearner: 0
            },
            cmi: {
                finishedIMIHours: 0,
                contractIMIHours: 0,
                contentPackages: 0,
                automatedSystem: false
            },
            cai: {
                courseLength: 0,
                TDYDays: 0,
                classroomsRequired: 0
            },
            live: {
                courseLength: 0,
                TDYDays: 0,
                classroomsRequired: 0
            }
        }
    }

    handleFieldChange = (e) => {
        let type = e.target.id.split('-')[0];
        const IDNum = e.target.id.split('-')[1];
        this.setState({
            [type]: {
                ...this.state[type],
                [IDNum]: e.target.value
            }
        })
    }

    handleTrainingType = (e) => {
        this.setState({
            selected: e.target.value
        })
    }
    
    render () {
        const { classes }  = this.props;
        let selectedTraining = null;
        switch (this.state.selected) {
            case 'cmi':
                selectedTraining = <CMI data={this.state} onChanged={(data) => this.handleFieldChange} />
                break;
            case 'cai':
                selectedTraining = <CAI data={this.state} onChanged={(data) => this.handleFieldChange} />
                break;
            case 'live':
                selectedTraining = <Live data={this.state} onChanged={(data) => this.handleFieldChange} />
                break;
            default:
                selectedTraining = <Typography variant="body1">Enter shared field values and select a method of instruction in the panel to the left.</Typography>;
                break;
        }
        
        return (
            <Grid container spacing={8} className={classes.root}>
                <Grid item xs={3}>
                    <Paper className={classes.paper}>
                        <Typography variant="h5" gutterBottom>Shared Data</Typography>
                        <Typography variant="body1" gutterBottom>Enter shared data values in the fields below:</Typography>

                        <TextField 
                            type="number" id="shared-learnerCostPerDay" fullWidth
                            className={classes.textField}
                            label="Classroom cost per day per learner"
                            onChange={this.handleFieldChange} value={this.state.shared.learnerCostPerDay} 
                            InputProps={{
                                startAdornment: <InputAdornment position="start">$</InputAdornment>
                              }}/>

                        <TextField 
                            type="number" id="shared-loadedAnnualInstructorCost" fullWidth
                            className={classes.textField}
                            label="Loaded annual cost for each instructor"
                            onChange={this.handleFieldChange} value={this.state.shared.loadedAnnualInstructorCost}
                            InputProps={{
                                startAdornment: <InputAdornment position="start">$</InputAdornment>
                              }}/>

                        <TextField 
                            type="number" id="shared-TDYCostPerDay" fullWidth
                            className={classes.textField}
                            label="TDY cost per day for each learner"
                            onChange={this.handleFieldChange} value={this.state.shared.TDYCostPerDay}
                            InputProps={{
                                startAdornment: <InputAdornment position="start">$</InputAdornment>
                              }} />

                        <TextField 
                            type="number" id="shared-learnerTargetAudience" fullWidth
                            className={classes.textField}
                            label="Total Learner Target Audience Throughput"
                            onChange={this.handleFieldChange} value={this.state.shared.learnerTargetAudience} />

                        <Divider className={classes.divider}/>

                        <Typography variant="h5" gutterBottom>IMI Method of Instruction Data</Typography>
                        <Typography variant="body1"gutterBottom>Select a method of instruction and enter values in the fields below:</Typography>
                        <RadioGroup
                            name="training-type"
                            value={this.state.selected}
                            onChange={this.handleTrainingType}
                            row>
                              <FormControlLabel value="cmi" control={<Radio />} label="CMI"/>
                              <FormControlLabel value="cai" control={<Radio />} label="CAI"/>
                              <FormControlLabel value="live" control={<Radio />} label="Live"/>
                        </RadioGroup>

                        <Fields data={this.state} onChanged={this.handleFieldChange}/>
                    </Paper>
                </Grid>
                <Grid item xs={9}>
                    <Paper className={classes.paper}>
                        {selectedTraining}
                    </Paper>
                </Grid>
            </Grid>
        );
    }
}

export default withStyles(styles)(CostCalculator);